import React, {useState} from 'react'
import {ClockBody, InnerPiece, HourHand, MinuteHand, Digit, DigitalClock} from './clockmain.elements'

const ClockMain = () => {
    function sleep(ms) {
        return new Promise(resolve => setTimeout(resolve, ms));
    }

    const formatTime = (x) => {
        if (x < 10) {
            return "0" + x;
        } else {
            return x;
        }
    }

    const initTime = () => {
        console.log("Time on load.")
        var d = new Date();
        var hours = d.getHours();
        var mins = d.getMinutes();
        hours = formatTime(hours);
        mins = formatTime(mins);
        return {hour: hours, minute: mins}
    }

    const [time, setTime] = useState(() => initTime());
    const currentH = time.hour;
    const currentM = time.minute;

    async function updateTime() {
        //await sleep(1000);
        var d = new Date();
        var hours = d.getHours();
        var mins = d.getMinutes();
        hours = formatTime(hours);
        mins = formatTime(mins);

        console.log({'check': hours + ':' + mins, 'state': time.hour + ':' + time.minute});
        setTime({hour: hours, minute: mins})
        var t = setTimeout(function(){ updateTime() }, 1000);
    }
updateTime();

//look into use effect


    return (
        <>
            <ClockBody>
                <InnerPiece>
                    <HourHand>
                        <div className="spacer1"></div>
                        <div className="hand"></div>
                        <div className="spacer2"></div>
                    </HourHand>
                    <MinuteHand>
                        <div className="spacer1"></div>
                        <div className="hand"></div>
                        <div className="spacer2"></div>
                    </MinuteHand>
                </InnerPiece>
                <Digit className="one" />
                <Digit className="two" />
                <Digit className="three" />
                <Digit className="four" />
            </ClockBody>
            <DigitalClock>
                <span id="hour">{currentH}</span>:<span id="minute">{currentM}</span>
            </DigitalClock>
        </>
    )
}

export default ClockMain
